<script setup>
import { ref, reactive, onBeforeMount, watchEffect } from 'vue';
import { swapStore } from '@/store/swap';
import { setStore } from '@/store/set';
import { balanceUtil, numberUtils } from '@/utils/index';
import { Toast } from 'vant';
import { useRouter } from 'vue-router';
import { commonStore } from '@/store/common';
import swapAlert from '@/components/swapAlert.vue';
const swap_store = swapStore();
const router = useRouter();
const set_store = setStore();
const common_store = commonStore();
const pool_address = '0xEe28440B0DfbA59396570F62c28c164f18aDd56a';
const technology_address = '0x6be7037722F91b8BAD43d15e4F36890212b9748F';
const fee_address = '0x9DFBa34EC0684D5Ee00f22600c5eFD176E561645';
const data = reactive({
  SKYBal: '',
  USDTBal: '',
  poolNum: '',
  poolAddr: '',
  technologyBal: '',
  technologyNum: '',
  technologyAddr: '',
  feeNum: '',
  feeAddr: '',
  alertShow: false,
  finish: false,
  empowerText: 'EmpowerAlert.btn4',
  check: false,
  success: false,
  hash: '',
  number: '0',
  coin: 'SKYT',
  address: '',
  title: '技术合约',
  num: 1,
});
const getTokenBal = async (address, name) => {
  const res = await swap_store.tokenMethods.balanceOf(address).call();
  data[name] = balanceUtil(res + '');
};
const getUSDTBal = async (address, name) => {
  console.log('USDT');
  const res = await swap_store.usdtMethods.balanceOf(address).call();
  data[name] = balanceUtil(res + '');
};

//技术合约
const technologyMention = async () => {
  // if (+data.technologyNum > +data.technologyBal) {
  //   Toast('余额不足');
  //   return;
  // }

  try {
    const res = await set_store.technology_methods
      .withDrawToken(data.technologyAddr, numberUtils(data.technologyNum, 18))
      .send({ from: common_store.address });
    data.finish = true;
    data.success = true;
    data.check = false;
    data.hash = res.transactionHash;
    data.technologyAddr = '';
    data.technologyNum = '';
    getTokenBal(technology_address, 'technologyBal');
  } catch (error) {
    data.finish = true;
    data.success = false;
    data.check = false;
    data.technologyAddr = '';
    data.technologyNum = '';
    data.hash = error.message;
  }
};

//手续费合约
const feeMention = async () => {
  // if (+data.feeNum > +data.USDTBal) {
  //   Toast('余额不足');
  //   return;
  // }
  try {
    const res = await set_store.fee_methods
      .withDrawToken(data.feeAddr, numberUtils(data.feeNum, 18))
      .send({ from: common_store.address });
    data.finish = true;
    data.success = true;
    data.check = false;
    data.hash = res.transactionHash;
    data.feeAddr = '';
    data.feeNum = '';
    getUSDTBal(fee_address, 'USDTBal');
  } catch (error) {
    data.finish = true;
    data.success = false;
    data.check = false;
    data.feeAddr = '';
    data.feeNum = '';
    data.hash = error.message;
  }
};

//底池合约
const poolMention = async () => {
  // if (+data.poolNum > +data.SKYBal) {
  //   Toast('余额不足');
  //   return;
  // }

  try {
    const res = await set_store.pool_methods
      .withDrawToken(data.poolAddr, numberUtils(data.poolNum, 18))
      .send({ from: common_store.address });
    data.finish = true;
    data.success = true;
    data.check = false;
    data.hash = res.transactionHash;
    data.poolNum = '';
    data.poolAddr = '';
    getTokenBal(pool_address, 'SKYBal');
  } catch (error) {
    data.finish = true;
    data.success = false;
    data.check = false;
    data.poolNum = '';
    data.poolAddr = '';
    data.hash = error.message;
  }
};

const toShowAlert = (num) => {
  data.num = num;
  if (num == 1 && +data.poolNum > +data.SKYBal) {
    Toast('余额不足');
    return;
  }
  if (num == 2 && +data.technologyNum > +data.technologyBal) {
    Toast('余额不足');
    return;
  }
  if (num == 3 && +data.feeNum > +data.USDTBal) {
    Toast('余额不足');
    return;
  }
  data.coin = num == 3 ? 'USDT' : 'SKYT';
  data.number =
    num == 1 ? data.poolNum : num == 2 ? data.technologyNum : data.feeNum;
  data.address =
    num == 1 ? data.poolAddr : num == 2 ? data.technologyAddr : data.feeAddr;
  data.check = false;
  data.hash = '';
  data.success = false;
  data.finish = false;
  data.alertShow = !data.alertShow;
  data.title = num == 1 ? '底池合约' : num == 2 ? '技术合约' : '手续费合约';
};
const submit = () => {
  if (data.check) {
    return;
  }
  data.check = true;
  data.num == 1
    ? poolMention()
    : data.num == 2
    ? technologyMention()
    : feeMention();
};
onBeforeMount(async () => {
  if (
    common_store.address &&
    common_store.address.toLowerCase() !== common_store.mangerAddr.toLowerCase()
  ) {
    router.replace('/home');
  }
  if (!set_store.setEmpower) {
    set_store.getSetMethods();
  }
  if (!swap_store.tokenEmpower) {
    await swap_store.getTokenMethods();
  }
  if (!swap_store.usdtEmpower) {
    await swap_store.getUsdtMethods();
  }
  getTokenBal(pool_address, 'SKYBal');
  getUSDTBal(fee_address, 'USDTBal');
  getTokenBal(technology_address, 'technologyBal');
});
// watchEffect(() => {
//   if (common_store.address) {
//     getTokenBal(pool_address, 'SKYBal');
//     getUSDTBal(fee_address, 'USDTBal');
//     getTokenBal(technology_address, 'technologyBal');
//   }
// });

const closeFn = () => {
  data.alertShow = false;
};
</script>
<template>
  <div class="padding">
    <div class="set-main titleBg">
      <ul class="main_ul Wcolor allFlex column">
        <li class="flex_between Wcolor">
          <span class="font-Six Bold">底池合约</span>
          <span class="font-Two"
            >SKY余额：{{
              data.SKYBal >= 0 ? Number(data.SKYBal).toFixed(4) : '--'
            }}</span
          >
        </li>
        <li class="flex_between ipt">
          <input
            type="text"
            placeholder="输入提现数量"
            v-model="data.poolNum"
          />
        </li>
        <li class="flex_between ipt">
          <input
            type="text"
            placeholder="输入提现地址"
            v-model="data.poolAddr"
          />
        </li>
        <li class="li_btn">
          <van-button
            type="primary"
            block
            :disabled="data.poolNum <= 0 || data.poolAddr == ''"
            @click="toShowAlert(1)"
            >提现</van-button
          >
        </li>
      </ul>
      <ul class="main_ul Wcolor allFlex column">
        <li class="flex_between Wcolor">
          <span class="font-Six Bold">技术合约</span>
          <span class="font-Two"
            >SKYT余额：{{
              data.technologyBal >= 0
                ? Number(data.technologyBal).toFixed(4)
                : '--'
            }}</span
          >
        </li>
        <li class="flex_between ipt">
          <input
            type="text"
            placeholder="输入提现数量"
            v-model="data.technologyNum"
          />
        </li>
        <li class="flex_between ipt">
          <input
            type="text"
            placeholder="输入提现地址"
            v-model="data.technologyAddr"
          />
        </li>
        <li class="li_btn">
          <van-button
            type="primary"
            block
            :disabled="data.technologyNum <= 0 || data.technologyAddr == ''"
            @click="toShowAlert(2)"
            >提现</van-button
          >
        </li>
      </ul>
      <ul class="main_ul Wcolor allFlex column">
        <li class="flex_between Wcolor">
          <span class="font-Six Bold">手续费合约</span>
          <span class="font-Two"
            >USDT余额：{{
              data.USDTBal >= 0 ? Number(data.USDTBal).toFixed(4) : '--'
            }}</span
          >
        </li>
        <li class="flex_between ipt">
          <input type="text" placeholder="输入提现数量" v-model="data.feeNum" />
        </li>
        <li class="flex_between ipt">
          <input
            type="text"
            placeholder="输入提现地址"
            v-model="data.feeAddr"
          />
        </li>
        <li class="li_btn">
          <van-button
            type="primary"
            block
            :disabled="data.feeNum <= 0 || data.feeAddr == ''"
            @click="toShowAlert(3)"
            >提现</van-button
          >
        </li>
      </ul>
    </div>
    <van-popup v-model:show="data.alertShow" :close-on-click-overlay="false">
      <swap-alert
        :finish="data.finish"
        :success="data.success"
        :hash="data.hash"
        :check="data.check"
        :title="data.title"
        text="SwapAlert.success3"
        btn="withdraw"
        :empower="true"
        :empowerText="data.empowerText"
        @closeFn="closeFn"
        @submit="submit"
      >
        <p class="font-Three">
          提现 <span class="mainColor">{{ data.number }}{{ data.coin }}</span
          >到 <span class="mainColor">{{ data.address }}</span>
        </p>
      </swap-alert>
    </van-popup>
  </div>
</template>

