  min-height: 400px;
  padding: 30px 15px;
  border-radius: 20px;
}
.main_ul {
  background: #35404d;
  border-radius: 15px;
  padding: 15px;
  // min-height: 240px;
  margin-bottom: 20px;
  input {
    width: 100%;
    height: 100%;
    &::placeholder {
      color: $greyColor;
    }
  }

  .ipt {
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #8c939a;
    margin-top: 10px;
  }
  .li_btn {
    margin-top: 20px;
  }
  .li-coin {
    margin-top: 10px;
  }
}
